export default {
	name: 'UniTag',
	props: {
		// 改变颜色
		mode: {
		    type: String,
		    value: "",
		    observer: '_changeMode',
		},
		list: {
		    type: Array,
		    value: [],
		    observer: '_changeList',
		},
		initindex: {
		    type: String,
		    value: 0,
		    observer: '_changeIndex',
		},
		// 改变颜色
		colorselect: {
		    type: String,
		    value: "#187bbf",
		},
		colorunselect: {
		    type: String,
		    value: "#000",
		},
		// 改变字体大小
		sizeselect: {
		    type: String,
		    value: "11pt",
		},
		sizeunselect: {
		    type: String,
		    value: "11pt",
		},
	},
	data() {
		return {
			MODE_SCROLL: "scroll",
			MODE_MENU: "menu", 
		}
	},
	methods: {
		_onClick() {
			if (this.disabled) {
				return
			}
			this.$emit('click')
		},
		click(e) {
			console.log(e)
			this.$emit('click',e)
		},
	}
}
